"use client"

import { useState, useEffect } from "react"
import { useParams } from "next/navigation"
import { useContract } from "@/context/contract-context"
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs"
import { Badge } from "@/components/ui/badge"
import { Skeleton } from "@/components/ui/skeleton"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"
import {
  CalendarClock,
  Clock,
  Vote,
  Trophy,
  AlertCircle,
  CheckCircle,
  Edit,
  Users,
  Info,
  ExternalLink,
} from "lucide-react"
import { formatAddress, formatDate, getVotingTypeText, getRestrictionText, getResultTypeText } from "@/lib/utils"
import VotingTab from "./_components/tabs/vote-tab"
import AdminTab from "./_components/tabs/admin-tab"
import OverviewTab from "./_components/tabs/overview-tab"
import RegistrationTab from "./_components/tabs/register-tab"
import ResultsTab from "./_components/tabs/results-tab"

export default function CampaignDetailsPage() {
  const params = useParams()
  const campaignId = params.id
  const { contract, address, isConnected, connectWallet } = useContract()
  const [campaignInfo, setCampaignInfo] = useState(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)
  const [isOwner, setIsOwner] = useState(false)
  const [userRegistered, setUserRegistered] = useState(false)
  const [userVoted, setUserVoted] = useState(false)
  const [campaignStatus, setCampaignStatus] = useState("pending") // pending, active, ended
  const [proposals, setProposals] = useState([])
  const [candidates, setCandidates] = useState([])

  useEffect(() => {
    const fetchCampaignDetails = async () => {
      if (!contract || !campaignId) return

      try {
        setLoading(true)
        setError(null)

        // Fetch basic campaign info
        const basicInfo = await contract.getCampaignBasicInfo(campaignId)
        const timeInfo = await contract.getCampaignTimeInfo(campaignId)
        const stats = await contract.getCampaignStats(campaignId)

        // Determine campaign status
        const now = Math.floor(Date.now() / 1000)
        let status = "pending"
        if (now > Number(timeInfo.endTime)) {
          status = "ended"
        } else if (now >= Number(timeInfo.startTime)) {
          status = "active"
        }
        setCampaignStatus(status)

        // Check if user is owner
        const isCreator = address && basicInfo.creator.toLowerCase() === address.toLowerCase()
        setIsOwner(isCreator)

        // Check if user is registered
        if (address) {
          const registrationType = await contract.getUserRegistrationType(campaignId, address)
          setUserRegistered(registrationType > 0)
          const hasVoted = await contract.hasUserVoted(campaignId, address)
          setUserVoted(hasVoted)
        }

        // Fetch proposals or candidates based on voting type
        if (basicInfo.votingType === 1) {
          // Proposal-based
          const proposalCount = await contract.getProposalIds(campaignId)
          const { contents, voteCounts } = await contract.getProposals(campaignId)

          const proposalData = Array.from({ length: Number(proposalCount) }, (_, i) => ({
            id: i,
            content: contents[i],
            voteCount: Number(voteCounts[i]),
          }))

          setProposals(proposalData)
        } else {
          // Candidate-based
          const candidateCount = await contract.getCandidateIds(campaignId)
          const { addresses, names, voteCounts } = await contract.getCandidates(campaignId)

          const candidateData = Array.from({ length: Number(candidateCount) }, (_, i) => ({
            id: i,
            address: addresses[i],
            name: names[i],
            voteCount: Number(voteCounts[i]),
            statement: "Candidate statement", // In a real app, you'd fetch this from the contract
          }))

          setCandidates(candidateData)
        }

        // Combine all data
        const campaignData = {
          id: campaignId,
          votingType: Number(basicInfo.votingType),
          restriction: Number(basicInfo.restriction),
          resultType: Number(basicInfo.resultType),
          creator: basicInfo.creator,
          description: basicInfo.description,
          startTime: Number(timeInfo.startTime) * 1000, // Convert to milliseconds
          endTime: Number(timeInfo.endTime) * 1000, // Convert to milliseconds
          status: Number(timeInfo.status),
          totalVotes: Number(stats.totalVotes),
          itemCount: Number(stats.itemCount),
          registeredVoterCount: Number(stats.registeredVoterCount),
        }

        setCampaignInfo(campaignData)
      } catch (err) {
        console.error("Error fetching campaign details:", err)
        setError("Failed to load campaign details. Please try again.")
      } finally {
        setLoading(false)
      }
    }

    if (contract && campaignId) {
      fetchCampaignDetails()
    }
  }, [contract, campaignId, address])

  const getStatusBadge = () => {
    switch (campaignStatus) {
      case "active":
        return (
          <Badge variant="outline" className="bg-green-50 text-green-600 border-green-200">
            <CheckCircle className="h-3 w-3 mr-1" />
            Active
          </Badge>
        )
      case "ended":
        return (
          <Badge variant="outline" className="bg-purple-50 text-purple-600 border-purple-200">
            <Trophy className="h-3 w-3 mr-1" />
            Ended
          </Badge>
        )
      default:
        return (
          <Badge variant="outline" className="bg-blue-50 text-blue-600 border-blue-200">
            <Clock className="h-3 w-3 mr-1" />
            Pending
          </Badge>
        )
    }
  }

  const isBeforeStart = campaignInfo && Date.now() < campaignInfo.startTime
  const isDuringVoting = campaignInfo && Date.now() >= campaignInfo.startTime && Date.now() <= campaignInfo.endTime
  const isAfterEnd = campaignInfo && Date.now() > campaignInfo.endTime

  // Not connected state
  if (!isConnected) {
    return (
      <div className="flex justify-center items-center min-h-[80vh]">
        <div className="w-full  px-4">
          <Card className="shadow-md border-gray-200">
            <CardHeader className="text-center">
              <CardTitle className="flex items-center justify-center gap-2">
                <Vote className="h-5 w-5" />
                Campaign Details
              </CardTitle>
              <CardDescription>Connect your wallet to view campaign details</CardDescription>
            </CardHeader>
            <CardContent className="flex flex-col items-center justify-center py-12">
              <Vote className="h-16 w-16 text-muted-foreground/30 mb-4" />
              <p className="text-muted-foreground text-center max-w-sm">
                Connect your wallet to view campaign details and participate in voting
              </p>
              <Button className="mt-6" onClick={connectWallet}>
                Connect Wallet
              </Button>
            </CardContent>
          </Card>
        </div>
      </div>
    )
  }

  // Loading state
  if (loading) {
    return (
      <div className="flex justify-center items-center py-8">
        <div className="w-full  px-4">
          <Card className="shadow-md border-gray-200">
            <CardHeader>
              <div className="flex items-center gap-2 mb-2">
                <Skeleton className="h-6 w-6 rounded-full" />
                <Skeleton className="h-8 w-1/3" />
              </div>
              <Skeleton className="h-4 w-1/2 mt-1" />
            </CardHeader>
            <CardContent>
              <div className="space-y-6">
                <Skeleton className="h-24 w-full" />
                <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                  <Skeleton className="h-32 w-full" />
                  <Skeleton className="h-32 w-full" />
                  <Skeleton className="h-32 w-full" />
                </div>
                <Skeleton className="h-10 w-full max-w-sm" />
                <Skeleton className="h-64 w-full" />
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    )
  }

  // Error state
  if (error || !campaignInfo) {
    return (
      <div className="flex justify-center items-center py-8">
        <div className="w-full max-w-xl px-4">
          <Alert variant="destructive" className="shadow-md">
            <AlertCircle className="h-4 w-4" />
            <AlertTitle>Error Loading Campaign</AlertTitle>
            <AlertDescription className="mt-2">
              {error || "Failed to load campaign details. Please try again later."}
            </AlertDescription>
            <Button variant="outline" className="mt-4 w-full" onClick={() => window.location.reload()}>
              Retry
            </Button>
          </Alert>
        </div>
      </div>
    )
  }

  // Main content
  return (
    <div className="flex justify-center items-center py-8">
      <div className="w-full px-4">
        <Card className="shadow-md border-gray-200">
          <CardHeader>
            <div className="flex items-start justify-between">
              <div>
                <div className="flex items-center gap-2">
                  <CardTitle className="flex items-center gap-2">
                    <Vote className="h-5 w-5" />
                    Campaign #{campaignId}
                  </CardTitle>
                  {getStatusBadge()}
                </div>
                <CardDescription className="mt-1">
                  Created by {isOwner ? "you" : formatAddress(campaignInfo.creator)}
                </CardDescription>
              </div>
              <div className="flex gap-2">
                {isOwner && (
                  <Button variant="outline" size="sm" className="gap-1">
                    <Edit className="h-4 w-4" />
                    Edit
                  </Button>
                )}
                <Button
                  variant="outline"
                  size="sm"
                  className="gap-1"
                  as="a"
                  href={`https://polygonscan.com/address/${campaignInfo.creator}`}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <ExternalLink className="h-4 w-4" />
                  Explorer
                </Button>
              </div>
            </div>
          </CardHeader>

          <CardContent>
            {/* Campaign Status Banner */}
            {isBeforeStart && campaignInfo.restriction !== 0 && (
              <Alert className="mb-4 bg-blue-50 text-blue-800 border-blue-200">
                <Info className="h-4 w-4 text-blue-500" />
                <AlertTitle>Registration Phase</AlertTitle>
                <AlertDescription>
                  This campaign is in the registration phase. Voting will begin at the scheduled start time.
                </AlertDescription>
              </Alert>
            )}

            {isBeforeStart && campaignInfo.restriction === 0 && (
              <Alert className="mb-4 bg-blue-50 text-blue-800 border-blue-200">
                <Info className="h-4 w-4 text-blue-500" />
                <AlertTitle>Voting Starts Soon</AlertTitle>
                <AlertDescription>
                  This campaign is open to all. Voting will begin at the scheduled start time.
                </AlertDescription>
              </Alert>
            )}

            {isDuringVoting && (
              <Alert className="mb-4 bg-green-50 text-green-800 border-green-200">
                <CheckCircle className="h-4 w-4 text-green-500" />
                <AlertTitle>Voting Active</AlertTitle>
                <AlertDescription>
                  This campaign is currently in the voting phase. Cast your vote before the deadline.
                </AlertDescription>
              </Alert>
            )}

            {isAfterEnd && (
              <Alert className="mb-4 bg-purple-50 text-purple-800 border-purple-200">
                <Trophy className="h-4 w-4 text-purple-500" />
                <AlertTitle>Voting Ended</AlertTitle>
                <AlertDescription>
                  This campaign has concluded. Check the results tab to see the outcome.
                </AlertDescription>
              </Alert>
            )}

            {/* Campaign Description */}
            <div className="mb-6">
              <h3 className="text-lg font-medium mb-2">Description</h3>
              <p className="text-muted-foreground whitespace-pre-wrap">{campaignInfo.description}</p>
            </div>

            {/* Campaign Info Cards */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-3 mb-6">
              {/* Campaign Type */}
              <Card className="bg-gray-50 border-gray-200">
                <CardContent className="p-4">
                  <div className="flex items-start gap-3">
                    <Vote className="h-5 w-5 text-primary mt-0.5" />
                    <div>
                      <h4 className="font-medium">Campaign Type</h4>
                      <div className="flex flex-wrap gap-2 mt-2">
                        <Badge variant="outline">{getVotingTypeText(campaignInfo.votingType)}</Badge>
                        <Badge variant="outline">{getRestrictionText(campaignInfo.restriction)}</Badge>
                        <Badge variant="outline">{getResultTypeText(campaignInfo.resultType)}</Badge>
                      </div>
                    </div>
                  </div>
                </CardContent>
              </Card>

              {/* Time Info */}
              <Card className="bg-gray-50 border-gray-200">
                <CardContent className="p-4">
                  <div className="flex items-start gap-3">
                    <CalendarClock className="h-5 w-5 text-primary mt-0.5" />
                    <div>
                      <h4 className="font-medium">Campaign Timeline</h4>
                      <div className="text-sm text-muted-foreground mt-2 space-y-1">
                        <div>
                          <span className="font-medium">Start:</span> {formatDate(campaignInfo.startTime)}
                        </div>
                        <div>
                          <span className="font-medium">End:</span> {formatDate(campaignInfo.endTime)}
                        </div>
                      </div>
                    </div>
                  </div>
                </CardContent>
              </Card>

              {/* Participation Info */}
              <Card className="bg-gray-50 border-gray-200">
                <CardContent className="p-4">
                  <div className="flex items-start gap-3">
                    <Users className="h-5 w-5 text-primary mt-0.5" />
                    <div>
                      <h4 className="font-medium">Participation</h4>
                      <div className="text-sm text-muted-foreground mt-2 space-y-1">
                        <div>
                          <span className="font-medium">Total Votes:</span> {campaignInfo.totalVotes}
                        </div>
                        {campaignInfo.restriction !== 0 && (
                          <div>
                            <span className="font-medium">Registered:</span> {campaignInfo.registeredVoterCount}
                          </div>
                        )}
                        <div>
                          <span className="font-medium">Your Status:</span>{" "}
                          {userVoted ? (
                            <Badge variant="outline" className="bg-green-50 text-green-600 border-green-200 ml-1">
                              Voted
                            </Badge>
                          ) : userRegistered ? (
                            <Badge variant="outline" className="bg-blue-50 text-blue-600 border-blue-200 ml-1">
                              Registered
                            </Badge>
                          ) : (
                            <Badge variant="outline" className="bg-gray-50 text-gray-600 border-gray-200 ml-1">
                              Not Registered
                            </Badge>
                          )}
                        </div>
                      </div>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </div>

            {/* Campaign Tabs */}
            <Tabs defaultValue={isAfterEnd ? "results" : isDuringVoting ? "voting" : "overview"} className="mt-6">
              <TabsList className="grid grid-cols-2 md:grid-cols-4 mb-6">
                <TabsTrigger value="overview">Overview</TabsTrigger>
                <TabsTrigger value="voting" disabled={!isDuringVoting && !isAfterEnd}>
                  Voting
                </TabsTrigger>
                {campaignInfo.restriction !== 0 && (
                  <TabsTrigger value="registration" disabled={isAfterEnd}>
                    Registration
                  </TabsTrigger>
                )}
                <TabsTrigger value="results" disabled={!isAfterEnd && !isOwner}>
                  Results
                </TabsTrigger>
                {isOwner && <TabsTrigger value="admin">Admin</TabsTrigger>}
              </TabsList>

              <TabsContent value="overview">
                <OverviewTab
                  campaignInfo={campaignInfo}
                  isOwner={isOwner}
                  isBeforeStart={isBeforeStart}
                  isDuringVoting={isDuringVoting}
                  isAfterEnd={isAfterEnd}
                  proposals={proposals}
                  candidates={candidates}
                />
              </TabsContent>

              <TabsContent value="voting">
                <VotingTab
                  campaignInfo={campaignInfo}
                  proposals={proposals}
                  candidates={candidates}
                  userRegistered={userRegistered}
                  userVoted={userVoted}
                  isOwner={isOwner}
                />
              </TabsContent>

              {campaignInfo.restriction !== 0 && (
                <TabsContent value="registration">
                  <RegistrationTab campaignInfo={campaignInfo} userRegistered={userRegistered} isOwner={isOwner} />
                </TabsContent>
              )}

              <TabsContent value="results">
                <ResultsTab campaignInfo={campaignInfo} proposals={proposals} candidates={candidates} isOwner={isOwner} />
              </TabsContent>

              {isOwner && (
                <TabsContent value="admin">
                  <AdminTab campaignInfo={campaignInfo} proposals={proposals} candidates={candidates} />
                </TabsContent>
              )}
            </Tabs>
          </CardContent>

          <CardFooter className="border-t pt-6 flex flex-col items-start">
            <h3 className="text-sm font-medium mb-2">Campaign Metadata</h3>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-x-8 gap-y-2 w-full text-sm">
              <div className="flex justify-between">
                <span className="text-muted-foreground">Campaign ID:</span>
                <span>{campaignId}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-muted-foreground">Creator:</span>
                <span className="font-mono">{formatAddress(campaignInfo.creator)}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-muted-foreground">Voting Type:</span>
                <span>{getVotingTypeText(campaignInfo.votingType)}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-muted-foreground">Restriction:</span>
                <span>{getRestrictionText(campaignInfo.restriction)}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-muted-foreground">Result Type:</span>
                <span>{getResultTypeText(campaignInfo.resultType)}</span>
              </div>
              <div className="flex justify-between">
                <span className="text-muted-foreground">Total Votes:</span>
                <span>{campaignInfo.totalVotes}</span>
              </div>
              {campaignInfo.restriction !== 0 && (
                <div className="flex justify-between">
                  <span className="text-muted-foreground">Registered Voters:</span>
                  <span>{campaignInfo.registeredVoterCount}</span>
                </div>
              )}
              <div className="flex justify-between">
                <span className="text-muted-foreground">Contract Address:</span>
                <span className="font-mono">{formatAddress(process.env.NEXT_PUBLIC_CONTRACT_ADDRESS)}</span>
              </div>
            </div>
          </CardFooter>
        </Card>
      </div>
    </div>
  )
}