"use client"

import { useState } from "react"
import { useContract } from "@/context/contract-context"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Textarea } from "@/components/ui/textarea"
import { Label } from "@/components/ui/label"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"
import { Separator } from "@/components/ui/separator"
import {
  AlertCircle,
  CheckCircle,
  Trash2,
  Plus,
  Settings,
  Clock,
  CalendarClock,
  Download, // Note: This import isn't used in the provided code, keep or remove as needed
  Info
} from "lucide-react"

export default function AdminTab({ campaignInfo, proposals, candidates }) {
  const { contract } = useContract()
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [error, setError] = useState(null)
  const [success, setSuccess] = useState(null)
  const [newProposal, setNewProposal] = useState("")
  const [showConfirmEnd, setShowConfirmEnd] = useState(false)
  const [showConfirmDelete, setShowConfirmDelete] = useState(false) // State for delete confirmation

  // --- Existing handlers ---
  const handleAddProposal = async () => {
    if (!newProposal.trim()) {
      setError("Please enter proposal content.")
      return
    }

    clearMessages() // Clear previous messages
    try {
      setIsSubmitting(true)

      // In a real app, you'd call the contract method to add a proposal
      await contract.addProposal(campaignInfo.id, newProposal)

      setSuccess("Proposal added successfully!")
      setNewProposal("")
      // TODO: Refresh campaign data after success
      // e.g., call a passed-in refresh function: `onDataRefresh()`
    } catch (err) {
      console.error("Error adding proposal:", err)
      setError(`Failed to add proposal: ${err.message || "Please try again"}`)
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleEndCampaign = async () => {
    clearMessages() // Clear previous messages
    try {
      setIsSubmitting(true)

      // In a real app, you'd call the contract method to end the campaign
      await contract.endCampaign(campaignInfo.id)

      setSuccess("Campaign ended successfully!")
      setShowConfirmEnd(false)
       // TODO: Refresh campaign data after success
      // e.g., call a passed-in refresh function: `onDataRefresh()`
    } catch (err) {
      console.error("Error ending campaign:", err)
      setError(`Failed to end campaign: ${err.message || "Please try again"}`)
    } finally {
      setIsSubmitting(false)
    }
  }

  const handleDeleteCampaign = async () => {
    clearMessages() // Clear previous messages
    try {
      setIsSubmitting(true)

      // In a real app, you'd call the contract method to delete the campaign
      // This is a placeholder for the actual contract call
      await contract.deleteCampaign(campaignInfo.id);

      setSuccess("Campaign deleted successfully! You may need to navigate away.") // Update success message
      setShowConfirmDelete(false)
      // TODO: Redirect user after successful deletion, e.g., back to campaigns list
      // router.push('/campaigns'); // if using Next.js App Router
    } catch (err) {
      console.error("Error deleting campaign:", err)
      setError(`Failed to delete campaign: ${err.message || "Please try again"}`)
    } finally {
      setIsSubmitting(false)
    }
  }

  const clearMessages = () => {
    setError(null)
    setSuccess(null)
  }

  // --- Render ---
  return (
    <div className="space-y-6"> {/* Increased spacing between sections */}
      <Card className="shadow-sm border-gray-200">
        <CardHeader className="pb-3">
          <CardTitle className="flex items-center gap-2 text-lg">
            <Settings className="h-5 w-5 text-gray-500" />
            Campaign Administration
          </CardTitle>
          <CardDescription>Manage your campaign settings and options</CardDescription>
        </CardHeader>
        <CardContent>
          {/* --- Message Alerts --- */}
          {error && (
            <Alert variant="destructive" className="mb-4 relative"> {/* Added relative for positioning */}
              <AlertCircle className="h-4 w-4" />
              <AlertTitle>Error</AlertTitle>
              <AlertDescription>{error}</AlertDescription>
              <Button
                variant="ghost"
                size="icon" // Make button smaller and square
                className="absolute top-1 right-1 h-6 w-6" // Adjusted positioning and size
                onClick={clearMessages}
              >
                <span className="sr-only">Close</span>
                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-4 h-4">
                  <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
                </svg>
              </Button>
            </Alert>
          )}

          {success && (
            <Alert className="bg-green-50 text-green-800 border-green-200 mb-4 relative"> {/* Added relative */}
              <CheckCircle className="h-4 w-4 text-green-500" />
              <AlertTitle>Success</AlertTitle>
              <AlertDescription>{success}</AlertDescription>
               <Button
                 variant="ghost"
                 size="icon" // Make button smaller and square
                 className="absolute top-1 right-1 h-6 w-6 text-green-800 hover:bg-green-100" // Adjusted positioning and size
                 onClick={clearMessages}
               >
                <span className="sr-only">Close</span>
                 <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-4 h-4">
                   <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
                 </svg>
               </Button>
            </Alert>
          )}

          <div className="space-y-6">
            {/* --- Campaign Status & End --- */}
            <div>
              <h3 className="text-md font-medium mb-3">Campaign Status</h3>
              <div className="flex items-center gap-2 mb-4 text-sm text-gray-600">
                <CalendarClock className="h-4 w-4" />
                <span>
                  Timeline: {new Date(campaignInfo.startTime).toLocaleString()} -{" "}
                  {new Date(campaignInfo.endTime).toLocaleString()}
                </span>
              </div>
              {/* Simplified status display - assuming campaignInfo.status exists */}
              <div className="mb-4 text-sm">
                Status: <span className={`font-medium ${campaignInfo.status === 2 ? 'text-red-600' : campaignInfo.status === 1 ? 'text-green-600' : 'text-gray-600'}`}>
                  {campaignInfo.status === 0 ? "Pending" : campaignInfo.status === 1 ? "Active" : "Ended"}
                </span>
              </div>


              {campaignInfo.status !== 2 && ( // Only show if campaign hasn't ended
                showConfirmEnd ? (
                  <div className="bg-amber-50 border border-amber-200 rounded-md p-4 mb-4">
                    <div className="flex items-start gap-3"> {/* Increased gap */}
                      <Info className="h-5 w-5 text-amber-500 mt-0.5 flex-shrink-0" />
                      <div>
                        <h4 className="font-medium text-amber-800">End Campaign Early?</h4>
                        <p className="text-sm text-amber-700 mt-1 mb-3">
                          This action will end voting immediately. All current votes will be counted, but no new votes can be cast. This cannot be undone.
                        </p>
                        <div className="flex flex-wrap gap-2"> {/* Added flex-wrap */}
                          <Button
                            variant="destructive"
                            size="sm"
                            onClick={handleEndCampaign}
                            disabled={isSubmitting}
                          >
                            {isSubmitting ? "Ending..." : "Confirm End Campaign"}
                          </Button>
                          <Button
                            variant="outline"
                            size="sm"
                            onClick={() => setShowConfirmEnd(false)}
                            disabled={isSubmitting}
                          >
                            Cancel
                          </Button>
                        </div>
                      </div>
                    </div>
                  </div>
                ) : (
                  <Button
                    variant="outline"
                    onClick={() => { clearMessages(); setShowConfirmEnd(true); }} // Clear messages before showing confirm
                    disabled={isSubmitting || campaignInfo.status === 2}
                    className="gap-2"
                  >
                    <Clock className="h-4 w-4" />
                    End Campaign Early
                  </Button>
                )
              )}
            </div>

            <Separator />

            {/* --- Add Proposal (Conditional) --- */}
            {campaignInfo.votingType === 1 && ( // Assuming 1 means proposal-based
              <div>
                <h3 className="text-md font-medium mb-3">Add Proposal</h3>
                <div className="space-y-3">
                  <div className="grid w-full gap-1.5"> {/* Use grid for label/textarea */}
                    <Label htmlFor="new-proposal">New Proposal Content</Label>
                    <Textarea
                      id="new-proposal"
                      placeholder="Enter the full text of the proposal..."
                      value={newProposal}
                      onChange={(e) => setNewProposal(e.target.value)}
                      rows={3}
                      className="resize-none"
                      disabled={isSubmitting}
                    />
                     <p className="text-sm text-muted-foreground">
                      Enter the proposal details clearly. Once added, it cannot be edited.
                    </p>
                  </div>
                  <Button
                    onClick={handleAddProposal}
                    disabled={!newProposal.trim() || isSubmitting}
                    className="gap-2 w-full sm:w-auto"
                  >
                    <Plus className="h-4 w-4" />
                    {isSubmitting ? "Adding..." : "Add Proposal"}
                  </Button>
                </div>
              </div>
            )}

            {/* Separator before Danger Zone */}
            {campaignInfo.votingType === 1 && <Separator />}

            {/* --- Danger Zone --- */}
            <div className="border border-red-200 bg-red-50 rounded-md p-4">
              <h3 className="text-md font-medium text-red-700 mb-3">Danger Zone</h3>
              <div className="space-y-4">
                 {/* --- Delete Confirmation --- */}
                {showConfirmDelete ? (
                  <div className="bg-white border border-red-300 rounded-md p-4">
                     <div className="flex items-start gap-3">
                       <AlertCircle className="h-5 w-5 text-red-500 mt-0.5 flex-shrink-0" />
                       <div>
                         <h4 className="font-medium text-red-800">Confirm Campaign Deletion</h4>
                         <p className="text-sm text-red-700 mt-1 mb-3">
                           This action is **irreversible**. All campaign data, including proposals/candidates and votes, will be permanently deleted from the contract. Are you absolutely sure?
                         </p>
                         <div className="flex flex-wrap gap-2">
                           <Button
                             variant="destructive"
                             size="sm"
                             onClick={handleDeleteCampaign}
                             disabled={isSubmitting}
                           >
                             {isSubmitting ? "Deleting..." : "Yes, Delete Campaign"}
                           </Button>
                           <Button
                             variant="outline"
                             size="sm"
                             onClick={() => setShowConfirmDelete(false)}
                             disabled={isSubmitting}
                           >
                             Cancel
                           </Button>
                         </div>
                       </div>
                     </div>
                  </div>
                ) : (
                  /* --- Delete Button --- */
                   <div>
                     <p className="text-sm text-red-600 mb-2">Permanently remove this campaign and all associated data.</p>
                     <Button
                       variant="destructive"
                       onClick={() => { clearMessages(); setShowConfirmDelete(true); }} // Clear messages before showing confirm
                       disabled={isSubmitting}
                       className="gap-2"
                     >
                       <Trash2 className="h-4 w-4" />
                       Delete Campaign
                     </Button>
                   </div>
                )}
              </div>
            </div>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}