"use client"

import { useState } from "react"
import { useContract } from "@/context/contract-context"
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"
import { UserPlus, AlertCircle, CheckCircle, Key } from "lucide-react"

export default function RegistrationTab({ campaignInfo, userRegistered, isOwner }) {
  const { contract } = useContract()
  const [passKey, setPassKey] = useState("")
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [error, setError] = useState(null)
  const [success, setSuccess] = useState(null)

  const handleRegister = async () => {
    try {
      setIsSubmitting(true)
      setError(null)
      setSuccess(null)

      // In a real app, you'd call the contract method to register
      await contract.registerToVote(campaignInfo.id, passKey)

      setSuccess("You have successfully registered for this campaign!")
      // In a real app, you'd want to refresh the campaign data here
    } catch (err) {
      console.error("Error registering:", err)
      setError("Failed to register. Please check your passkey and try again.")
    } finally {
      setIsSubmitting(false)
    }
  }

  if (userRegistered) {
    return (
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <CheckCircle className="h-5 w-5 text-green-500" />
            Registration Complete
          </CardTitle>
          <CardDescription>You are registered for this campaign</CardDescription>
        </CardHeader>
        <CardContent>
          <Alert className="bg-green-50 text-green-800 border-green-200">
            <CheckCircle className="h-4 w-4 text-green-500" />
            <AlertTitle>Successfully Registered</AlertTitle>
            <AlertDescription>
              You are registered for this campaign. You can now vote during the voting period.
            </AlertDescription>
          </Alert>

          <div className="mt-6 border rounded-md p-4 bg-muted/30">
            <h3 className="font-medium mb-2">Registration Details</h3>
            <div className="text-sm text-muted-foreground space-y-2">
              <div className="flex justify-between">
                <span>Registration Type:</span>
                <span>Voter</span>
              </div>
              <div className="flex justify-between">
                <span>Campaign ID:</span>
                <span>{campaignInfo.id}</span>
              </div>
              <div className="flex justify-between">
                <span>Status:</span>
                <span className="text-green-600">Active</span>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>
    )
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <UserPlus className="h-5 w-5 text-primary" />
          Register for Campaign
        </CardTitle>
        <CardDescription>Register to participate in this campaign</CardDescription>
      </CardHeader>
      <CardContent>
        {error && (
          <Alert variant="destructive" className="mb-4">
            <AlertCircle className="h-4 w-4" />
            <AlertTitle>Error</AlertTitle>
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}

        {success && (
          <Alert className="bg-green-50 text-green-800 border-green-200 mb-4">
            <CheckCircle className="h-4 w-4 text-green-500" />
            <AlertTitle>Success</AlertTitle>
            <AlertDescription>{success}</AlertDescription>
          </Alert>
        )}

        <div className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="passkey">Passkey</Label>
            <div className="relative">
              <Key className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
              <Input
                id="passkey"
                type="password"
                placeholder="Enter campaign passkey"
                className="pl-9"
                value={passKey}
                onChange={(e) => setPassKey(e.target.value)}
              />
            </div>
            <p className="text-xs text-muted-foreground">
              Enter the passkey provided by the campaign creator to register.
            </p>
          </div>

          <Button className="w-full" onClick={handleRegister} disabled={!passKey || isSubmitting}>
            {isSubmitting ? "Registering..." : "Register"}
          </Button>
        </div>
      </CardContent>
      <CardFooter className="border-t pt-4 flex flex-col items-start">
        <h3 className="text-sm font-medium mb-2">Registration Information</h3>
        <p className="text-sm text-muted-foreground">
          {campaignInfo.restriction === 1
            ? `This campaign has limited registration. Only ${campaignInfo.maxVoters} voters can register.`
            : "This campaign requires registration before you can vote. Once registered, you will receive a non-transferable NFT that represents your voting right."}
        </p>
      </CardFooter>
    </Card>
  )
}
