"use client"

import { useState } from "react"
import { useContract } from "@/context/contract-context"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group"
import { Label } from "@/components/ui/label"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"
import { Vote, AlertCircle, CheckCircle, Lock } from "lucide-react"
import { formatAddress } from "@/lib/utils"

export default function VotingTab({ campaignInfo, proposals, candidates, userRegistered, userVoted, isOwner }) {
  const { contract } = useContract()
  const [selectedOption, setSelectedOption] = useState(null)
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [error, setError] = useState(null)
  const [success, setSuccess] = useState(null)

  const handleVote = async () => {
    if (!selectedOption) {
      setError("Please select an option to vote for.")
      return
    }

    try {
      setIsSubmitting(true)
      setError(null)
      setSuccess(null)

      if (campaignInfo.votingType === 1) {
        // Proposal-based voting
        await contract.voteForProposal(campaignInfo.id, selectedOption)
      } else {
        // Candidate-based voting
        await contract.voteForCandidate(campaignInfo.id, selectedOption)
      }

      setSuccess("Your vote has been cast successfully!")
      // In a real app, you'd want to refresh the campaign data here
    } catch (err) {
      console.error("Error casting vote:", err)
      setError("Failed to cast your vote. Please try again.")
    } finally {
      setIsSubmitting(false)
    }
  }

  if (userVoted) {
    return (
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <CheckCircle className="h-5 w-5 text-green-500" />
            Vote Cast
          </CardTitle>
          <CardDescription>You have already voted in this campaign</CardDescription>
        </CardHeader>
        <CardContent>
          <Alert className="bg-green-50 text-green-800 border-green-200">
            <CheckCircle className="h-4 w-4 text-green-500" />
            <AlertTitle>Thank you for voting!</AlertTitle>
            <AlertDescription>
              Your vote has been recorded. You can view the results after the campaign ends.
            </AlertDescription>
          </Alert>
        </CardContent>
      </Card>
    )
  }

  if (campaignInfo.restriction !== 0 && !userRegistered) {
    return (
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center gap-2">
            <Lock className="h-5 w-5 text-amber-500" />
            Registration Required
          </CardTitle>
          <CardDescription>You need to register before you can vote in this campaign</CardDescription>
        </CardHeader>
        <CardContent>
          <Alert className="bg-amber-50 text-amber-800 border-amber-200">
            <AlertCircle className="h-4 w-4 text-amber-500" />
            <AlertTitle>Registration Required</AlertTitle>
            <AlertDescription>
              This campaign requires registration before voting. Please go to the Registration tab to register.
            </AlertDescription>
          </Alert>
          <Button className="mt-4" variant="outline">
            Go to Registration
          </Button>
        </CardContent>
      </Card>
    )
  }

  return (
    <Card>
      <CardHeader>
        <CardTitle className="flex items-center gap-2">
          <Vote className="h-5 w-5 text-primary" />
          Cast Your Vote
        </CardTitle>
        <CardDescription>
          {campaignInfo.votingType === 1 ? "Select a proposal to vote for" : "Select a candidate to vote for"}
        </CardDescription>
      </CardHeader>
      <CardContent>
        {error && (
          <Alert variant="destructive" className="mb-4">
            <AlertCircle className="h-4 w-4" />
            <AlertTitle>Error</AlertTitle>
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}

        {success && (
          <Alert className="bg-green-50 text-green-800 border-green-200 mb-4">
            <CheckCircle className="h-4 w-4 text-green-500" />
            <AlertTitle>Success</AlertTitle>
            <AlertDescription>{success}</AlertDescription>
          </Alert>
        )}

        <RadioGroup
          value={selectedOption !== null ? selectedOption.toString() : undefined}
          onValueChange={(value) => setSelectedOption(Number.parseInt(value))}
          className="space-y-4"
        >
          {campaignInfo.votingType === 1
            ? proposals.map((proposal) => (
                <div
                  key={proposal.id}
                  className="flex items-start space-x-2 border rounded-md p-4 hover:bg-muted/50 transition-colors"
                >
                  <RadioGroupItem value={proposal.id.toString()} id={`proposal-${proposal.id}`} className="mt-1" />
                  <div className="flex-1">
                    <Label htmlFor={`proposal-${proposal.id}`} className="font-medium cursor-pointer">
                      Proposal {proposal.id + 1}
                    </Label>
                    <p className="text-sm text-muted-foreground mt-1">{proposal.content}</p>
                  </div>
                </div>
              ))
            : candidates.map((candidate) => (
                <div
                  key={candidate.id}
                  className="flex items-start space-x-2 border rounded-md p-4 hover:bg-muted/50 transition-colors"
                >
                  <RadioGroupItem value={candidate.id.toString()} id={`candidate-${candidate.id}`} className="mt-1" />
                  <div className="flex-1">
                    <Label htmlFor={`candidate-${candidate.id}`} className="font-medium cursor-pointer">
                      {candidate.name}
                    </Label>
                    <p className="text-xs text-muted-foreground">{formatAddress(candidate.address)}</p>
                    <p className="text-sm text-muted-foreground mt-1">{candidate.statement}</p>
                  </div>
                </div>
              ))}
        </RadioGroup>

        <Button className="w-full mt-6" onClick={handleVote} disabled={selectedOption === null || isSubmitting}>
          {isSubmitting ? "Casting Vote..." : "Cast Vote"}
        </Button>
      </CardContent>
    </Card>
  )
}
